import * as React from "react"
import NextApp, { Container, DefaultAppIProps, AppProps as NextAppProps } from "next/app"
import Head from "next/head"
import { MuiThemeProvider, CssBaseline } from "@material-ui/core"
import { JssProvider } from "react-jss"
import { library } from "@fortawesome/fontawesome-svg-core"
import { fab } from "@fortawesome/free-brands-svg-icons"
import { fas } from "@fortawesome/free-solid-svg-icons"
import { getPageContext } from "../helpers/get-page-context"
import Page from "../components/page"

library.add(fab, fas)

const injectGoogleAnalytics = (): void => {
  if (typeof window !== "undefined") {
    window.dataLayer = window.dataLayer || []
    function gtag(...args) {
      window.dataLayer.push(args)
    }
    gtag("js", new Date())
    gtag("config", "UA-126885958-3")
  }
}

interface AppProps {
  Component: React.Component
  pageProps: any
}

class App extends NextApp<AppProps & DefaultAppIProps & NextAppProps> {
  pageContext: any

  constructor(props: AppProps & any) {
    super(props)
    this.pageContext = getPageContext()
  }

  static async getInitialProps({ Component, ctx }): Promise<any> {
    let pageProps: any = {}
    if (Component.getInitialProps) {
      pageProps = await Component.getInitialProps(ctx)
    }
    // this exposes the query to the user
    pageProps.query = ctx.query
    return { pageProps }
  }

  componentDidMount = (): void => {
    // Remove the server-side injected CSS.
    const jssStyles = document.querySelector("#jss-server-side")
    if (jssStyles && jssStyles.parentNode) {
      jssStyles.parentNode.removeChild(jssStyles)
    }
  }

  render = (): JSX.Element => {
    const { Component, pageProps } = this.props
    return (
      <Container>
        <Head>
          <script src="https://js.stripe.com/v3/" />
          <meta
            name="description"
            content="Give the gift of crypto this holiday season with a crypto ecard!"
          />
          <meta
            name="keywords"
            content="ecard,gifts,cryptocurrency,bitcoin,ethereum,altcoins,broadhaven,gift card, alt coins, shit coins, christmas"
          />
          <meta name="author" content="Tim Holmes-Mitra <tim.holmes.mitra@broadhaven.tech>" />

          <script async src="https://www.googletagmanager.com/gtag/js?id=UA-126885958-3" />
          <script>{injectGoogleAnalytics()}</script>
          <title>Cards Of Crypto</title>
        </Head>
        <JssProvider
          registry={this.pageContext.sheetsRegistry}
          generateClassName={this.pageContext.generateClassName}
        >
          <MuiThemeProvider theme={this.pageContext.theme} sheetsManager={this.pageContext.sheetsManager}>
            <CssBaseline />
            <div className="root">
              <Page pageContext={this.pageContext}>
                <Component {...pageProps} pageContext={this.pageContext} />
              </Page>
            </div>
          </MuiThemeProvider>
        </JssProvider>
      </Container>
    )
  }
}

export default App
